/* eslint-disable */
import React, { Component } from "react";
import { navigate } from "@reach/router";
import {
    Dropdown,
    DropdownToggle,
    DropdownMenu,
    DropdownItem,
} from "reactstrap";

import {
    FacebookShareButton,
    TwitterShareButton,
    TelegramShareButton,
    WhatsappShareButton,
} from "react-share";

export const getShareButton = (job, dropdownOpen, toggle) => (
    <Dropdown isOpen={dropdownOpen} toggle={toggle} className="pr-5">
        <DropdownToggle caret className="btn btn-success  text-left ">
            <i className="fa fa-share-alt" /> &nbsp; Share Job
        </DropdownToggle>
        {typeof window !== `undefined` && job !== undefined && (
            <DropdownMenu bottom>
                <DropdownItem>
                    <FacebookShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none dropdown-item text-left btn  btn-block b-0 text-primary bg-white">
                            <i className=" fa fa-facebook-f " />
                            &nbsp;&nbsp;Facebook
                        </button>
                    </FacebookShareButton>
                </DropdownItem>
                <DropdownItem>
                    <TwitterShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none dropdown-item text-left btn o btn-block text-info bg-white">
                            <i className=" fa fa-twitter " />
                            &nbsp;&nbsp;Twitter
                        </button>
                    </TwitterShareButton>
                </DropdownItem>
                <DropdownItem>
                    <WhatsappShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none dropdown-item text-left btn  btn-block text-success bg-white">
                            <i className=" fa fa-whatsapp " />
                            &nbsp;&nbsp;Whatsapp
                        </button>
                    </WhatsappShareButton>
                </DropdownItem>
                <DropdownItem>
                    <TelegramShareButton
                        url={window.location.href}
                        quote={job.text}
                    >
                        <button className="px-3 border-focus-none  dropdown-item text-left btn  btn-block text-info bg-white">
                            <i className=" fa fa-telegram " />
                            &nbsp;&nbsp;Telegram
                        </button>
                    </TelegramShareButton>
                </DropdownItem>
            </DropdownMenu>
        )}
    </Dropdown>
);

class PositionCard extends Component {
    getCurrentWidth = () => {
        let screenWidth = null;
        if (typeof window !== `undefined`) {
            screenWidth = window.screen.width;
        }
        return screenWidth;
    };

    getCurrentHeight = () => {
        let screenHeight = null;
        if (typeof window !== `undefined`) {
            screenHeight = window.screen.height;
        }
        return screenHeight;
    };

    onClickCloseModal = (e, job) => {
        e.stopPropagation();

        navigate(`/careers/${job.id}`);
    };
    render() {
        return (
            <div>
                <div className="d-flex flex-row flex-wrap pb-5">
                    {this.props.jobsData.map((job, i) => {
                        console.log("job id", job);
                        return (
                            <div
                                id={job.node.id}
                                name={job.node.id}
                                key={i}
                                className="col-12 col-md-6 mt-4 mb-0 px-2 col-lg-4"
                            >
                                {/* eslint-disable-next-line */}
                                <div
                                    onClick={() => {
                                        {
                                            navigate(`/careers/${job.node.id}`);
                                        }
                                    }}
                                    style={{
                                        minHeight:
                                            this.getCurrentWidth() >= 768
                                                ? "95px"
                                                : "90px",
                                        borderRadius: "10px",
                                    }}
                                    className={` scroll  d-flex flex-column flex-wrap justify-content-center careers-position careers-position-unhighlight`}
                                >
                                    {/* <Link to={{}} className=""> */}
                                    <h6 className=" px-4 roboto-regular font-sm text-dark ">
                                        {job.node.text}
                                    </h6>
                                    <div className="d-flex flex-row flex-wrap roboto-black font-xs text-uppercase">
                                        <span className="pl-4  text-green  col-">
                                            {job.node.categories.location}
                                        </span>
                                        <span className="pl-4  text-black  col-">
                                            {job.node.categories.team}
                                        </span>
                                    </div>
                                    {/* </Link> */}
                                    {this.props.location.search.split(
                                        "="
                                    )[3] === job.node.id && (
                                        <div>
                                            {
                                                <div className="pl-4 pt-3">
                                                    <div className="row justify-content-between align-items-center pl-4"></div>
                                                    <div className="d-block text-center" />
                                                </div>
                                            }
                                        </div>
                                    )}
                                </div>
                            </div>
                        );
                    })}
                    {this.props.jobsData.length === 0 && (
                        <h6 className="maison-bold mx-auto font-xl-l">
                            No Results Found
                        </h6>
                    )}
                </div>

                {/* <div
          onClick={() => this.props.onChangeURL(this.props.id)}


        >
          {

          }
          <p className="  px-1 text-success roboto-bold">
            {this.props.subHeading}
          </p>
        </div>
        {this.props.id === this.props.positionId && (
          <div className="col-12 bg-success py-1 " />
        )}*/}
            </div>
        );
    }
}

export default PositionCard;
